/* Font Imports  */
@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: local('Roboto'), local('Roboto-Regular'), url('/assets/fonts/Roboto-Regular.ttf') format('truetype');
}
@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    src: local('Roboto Medium'), local('Roboto-Medium'), url('/assets/fonts/Roboto-Medium.ttf') format('truetype');
}
@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    src: local('Roboto Bold'), local('Roboto-Bold'), url('/assets/fonts/Roboto-Bold.ttf') format('truetype');
}

$max-width: 1400px;
$max-scroll: 20em;

/* Custom CSS Styles */
body {
	padding-top: 75px;
}
.navbar-brand > img {
    max-height: 2em;
    width: auto;
    -moz-padding: 15px; /* firefox bug fix */
}
.container-fluid .row {
    max-width: $max-width;
}
#alert-container {
	z-index: 100;
	position: fixed;
	bottom: 1em;
	right: 1em;
}
.code, textarea {
  font-family: 'Roboto', Consolas, monaco, monospace;
	font-size: 1em;
	font-style: normal;
	font-variant: normal;
	font-weight: 400;
	line-height: 18.5714px;
	white-space: pre-wrap;
}
div {
	&.scroll {
		max-height: $max-scroll;
		overflow: scroll;
		&-lg {
			max-height: $max-scroll*2;
			overflow: scroll;
		}
		&-xl {
			max-height: $max-scroll*3;
			overflow: scroll;
		}
	}
}
/* Pretty JSON Module theme */
.json-pretty {
	font-family: 'Roboto', monaco, monospace;
	font-size: 0.8zem;
  line-height: inherit;
	color: #D4D4D4;
	background-color: #FCFDFD;
	.json {
		&-key {
			color: #59A5D8;
		}
		&-value, &-boolean {
			color: #386FA4;
		}
		&-string {
			color: #FA7921;
		}
	}
}

.modal {
    text-align: center;
    padding: 0!important;
    &:before {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &-dialog {
        display: inline-block;
        text-align: left;
        vertical-align: middle;
        min-width: 350px;
    }
}

.nav-item {
    cursor: pointer;
}

//Schema Creator Styles
#schema-options {
    .list-group-item {
        padding: 5px;
        &-action {
            cursor: pointer;
        }
    }
}

